<template>
	<view class="personal" :style="{height:containerHeight}">
		<view class="infor-list">
			<uni-forms ref="valiForm" :rules="rules" :modelValue="valiFormData" validate-trigger="bind">
				<uni-forms-item class="list" label="Original Password" required name="password" label-width="40%">
					<input v-model="valiFormData.password" placeholder="Please Enter" :password="password" />
					<view :class="[!password ? 'eyeBtn' : 'eyeClass']" @click="getpassword"></view>
				</uni-forms-item>
				<uni-forms-item class="list" label="New Password" required name="newPassword" label-width="40%">
					<!-- <uni-easyinput v-model="valiFormData.newPassword" placeholder="Please Enter" type="password" /> -->
					<input v-model="valiFormData.newPassword" placeholder="Please Enter" :password="newPassword" />
					<view :class="[!newPassword ? 'eyeBtn' : 'eyeClass']" @click="getnewPassword"></view>
				</uni-forms-item>
				<uni-forms-item class="list" label="Comfirm Password" required name="comfirmPassword" label-width="40%">
					<input v-model="valiFormData.comfirmPassword" placeholder="Please Enter"
						:password="comfirmPassword" />
					<view :class="[!comfirmPassword ? 'eyeBtn' : 'eyeClass']" @click="getcomfirmPassword"></view>
				</uni-forms-item>
			</uni-forms>
		</view>

		<view class="bottom-btn">
			<button type="default" style="background-color: #193c47;color: #FFFFFF;" class="submit"
				@click="submit()">Save</button>
		</view>
	</view>


</template>

<script>
	import api from "@/api/api";
	import {
		ACCESS_TOKEN,
		USER_NAME,
		USER_INFO,
		USER_ID
	} from "@/common/util/constants"
	export default {
		data() {
			return {
				firstLogin:"1",
				containerHeight: '800px',
				patternRules: "",
				password: true,
				newPassword: true,
				comfirmPassword: true,
				errorMessage: '',
				valiFormData: {
					password: '',
					newPassword: '',
					comfirmPassword: ''
				},
				required: {
					password:true,
					newPassword: true,
					comfirmPassword:false
				},
				// 校验规则
				requiredArr: [
					'password',
					'newPassword',
					'comfirmPassword'
				],
				rules: {
					password: {
						rules: [{
							required: true, errorMessage: 'The User Cannot Be Empty.'
						}]
					},
					newPassword: {
						rules: [{
							required: true, errorMessage: 'The New Password Cannot Be Empty.'
						}]
					},
					comfirmPassword: {
						rules: [
							{ required: true, errorMessage: 'The ComfirmPassword Cannot Be Empty.' },
							{ 
								validateFunction:function(rule,value,data,callback){
								if (value && value !== data.newPassword) {
								    callback('The Two Password Entries Are Different!')
								} else {
									callback()
								}
								return true
								},
							}
						]
					},
				},
			}
		},
		created() {
			const info = uni.getSystemInfoSync();
			if (info.uniPlatform.toLowerCase() === 'app') {
			    plus.navigator.setStatusBarStyle("dark")
			}
			this.getPatternRules()
			// this.$store.dispatch('fetchUserRole',this.$store.getters.userid);
		},
		onLoad(e) {
			this.getScreenHeight()
			const info = uni.getSystemInfoSync();
			if(e && e.firstLogin){
				this.firstLogin = e.firstLogin
			}
			if (info.uniPlatform.toLowerCase() === 'app') {
			    plus.navigator.setStatusBarStyle("dark")
			}
		},
		onShow(){
			const info = uni.getSystemInfoSync();
			if (info.uniPlatform.toLowerCase() === 'app') {
			    plus.navigator.setStatusBarStyle("dark")
			}
		},
		methods: {
			getPatternRules() {
				let userRole = this.$store.state.userRole
				if(userRole.includes("admin") || userRole.includes("test_sys_admin")){
					this.patternRules =
						/^(?=.*[A-Z]|.*[\W_])(?=.*[A-Z]|.*[a-z])(?=.*[A-Z]|.*\d)(?=.*[a-z]|.*[\W_])(?=.*[a-z]|.*\d)(?=.*\d|.*[\W_]).{15,}$/
					this.errorMessage =
						'A 15-Bit Combination (Uppercase Letters, Lowercase Letters, Numbers And Special Characters) Containing At Least Three Of the Following Four Conditions.'
				}else{
					this.patternRules =
						/^(?=.*[A-Z]|.*[\W_])(?=.*[A-Z]|.*[a-z])(?=.*[A-Z]|.*\d)(?=.*[a-z]|.*[\W_])(?=.*[a-z]|.*\d)(?=.*\d|.*[\W_]).{12,}$/
					this.errorMessage =
						'A 12-Bit Combination (Uppercase Letters, Lowercase Letters, Numbers And Special Characters) Containing At Least Three Of The Following four Conditions.'
				}
				this.$set(this.rules, 'newPassword', {
					rules: [{
							required: true,
							errorMessage: 'The NewPassword Cannot Be Empty.'
						},
						{
							pattern: this.patternRules,
							errorMessage: this.errorMessage
						},
						{
							validateFunction:function(rule,value,data,callback){
							if (value && value == data.password) {
							    callback('The New Password Cannot Be The Same As The Old Password!')
							} else {
								callback()
							}
							return true
							},
						}
					],
					label: 'newPassword',
					validateTrigger: 'submit'
				});
			},
			getScreenHeight() {
				uni.getSystemInfo({
					success: (res) => {
						this.containerHeight = res.windowHeight + 'px';
					}
				});
			},
			getpassword() {
				this.password = !this.password;
			},
			getnewPassword() {
				this.newPassword = !this.newPassword;
			},
			getcomfirmPassword() {
				this.comfirmPassword = !this.comfirmPassword;
			},
			submit() {
				let that = this
				this.$refs.valiForm.validateField(this.requiredArr).then((res) => {
					if (that.valiFormData != null) {
						let sm2 = require('sm-crypto').sm2
						let publicKey =
							'0494006b520344b99a1fcdf1b498281fa37abb391c925cdc30a59be27507add5b74ae062d8329e0147c272d5eabcb503a5ffa3ac8c0dcbd60b5e6464d9a088411b'
						let params = {
							// confirmpassword: sm2.doEncrypt(this.valiFormData.comfirmPassword, publicKey),
							password: sm2.doEncrypt(this.valiFormData.newPassword, publicKey),
							username: this.$store.state.user.username
						}
						this.$http.put(this.$apiUrl + '/sys/user/changeMyPassword', params).then((res) => {
							if (res.data.success) {
								uni.showToast({
									icon: 'none',
									title: 'The Password Is Changed Successfully. Please Log In Again'
								});
								setTimeout(function() {
									api.logout().then(res => {
										uni.clearStorageSync()
										uni.navigateTo({
											url: '/pages/login/login'
										})
									})
								}, 1500)
							} else {
								uni.showToast({
									icon: 'error',
									title: 'Modification Failure'
								});
							}
						})
					}
				}).catch((err) => {
					console.log('err', err);
				})
			}
		}
	}
</script>

<style lang="scss" scoped scoped>
	.eyeBtn {
		width: 22px;
		height: 22px;
		top: 15px;
		right: 10px;
		z-index: 9;
		background: url(@/static/pass-hide@3x.png) no-repeat center center;
		background-size: 20px;
	}

	.eyeClass {
		width: 22px;
		height: 22px;
		top: 15px;
		right: 10px;
		z-index: 9;
		background: url(@/static/pass-show@3x.png) no-repeat center center;
		background-size: 20px;
	}

	::v-deep  .uni-forms-item__content {
		display: flex;
		justify-content: space-between;
	}


	.personal {
		width: 100%;
		background-color: #f2f2f2;
		margin-bottom: 152upx;
		padding-top: env(safe-area-inset-top); /* 兼容 iOS 11.0-11.4 */
		padding-top: constant(safe-area-inset-top); /* 兼容 iOS 12.0-12.1 */
		// padding-top: 25px;
	}

	.bottom-btn {
		position: fixed;
		left: 0;
		right: 0;
		bottom: 0;
		box-sizing: border-box;
		padding: 20upx 32upx;
		background-color: #FFFFFF;

		.submit {
			border-radius: 10upx;
		}
	}

	.infor-list {
		display: block;
		width: 100%;
		color: black !important;
		box-sizing: border-box;

		// padding: 0 32upx;
		.lis-title {
			padding: 0 32upx;
			line-height: 72upx;
			color: #777777;
			position: relative;
		}

		.lis-title::before {
			content: '';
			position: absolute;
			left: 16upx;
			height: 36%;
			width: 6upx;
			background-color: #0289fe;
			top: 24upx;
			border-radius: 8upx;
		}

		.list {
			margin-bottom: 16upx;
			padding: 0 32upx;
			background-color: #FFFFFF;
			box-sizing: border-box;
			width: 100%;
			height: 240upx;
			overflow: auto;
			display: flex;
			align-items: center;
			border-bottom: 1upx solid #ececec;

			.list-part {
				flex: 1;

				.title-time {
					display: flex;
					justify-content: space-between;
					align-items: center;

					text:first-of-type {
						padding-right: 20upx;
						color: #262626;
						font-size: 30upx;
					}

					input {
						font-size: 30upx;
						color: #9e9e9e;
						line-height: 56upx;
						flex: 1;
						text-align: right;
					}

					.uni-input-placeholder {
						color: #CCCCCC;
					}
				}
			}
		}
	}

	::v-deep .uni-data-checklist .checklist-group .checklist-box {
		margin-right: 0;
	}

	::v-deep .uni-data-checklist .checklist-group {
		justify-content: flex-end;
	}
</style>